@import url("https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap");

.App {
  text-align: center;
  font-family: "Press Start 2P", cursive;
}

.pokedex {
  background-color: #2c3484;
  padding: 10px;
}

h2 {
  color: #f42c2c;
}

.pokemons {
  font-size: 1em;
  margin-top: 15px;
  cursor: pointer;
}

.pokenames {
  text-decoration: none;
  color: #2c3484;
  font-weight: 1000;
  font-family: "Press Start 2P", sans-serif;
}

.details {
  margin-top: 20px;
  font-family: "Montserrat", sans-serif;
  font-size: 1.5em;
  color: #2c3484;
}

.pokemon-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
}

.pokemon-grid > * {
  width: 100%;
}

.detail-img {
  border-bottom: 1px solid black;
}

.pagination-buttons {
  display: flex;
  justify-content: center;
  margin-top:25px;
}

.pagination-buttons button {
  padding: 20px;
  font-size: 16px;
  background-color: red;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.return-button {
  padding: 20px;
  font-size: 16px;
  background-color: red;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.pagination-buttons button:disabled {
  background-color: lightgray;
  cursor: not-allowed;
}
